<template>
  <el-row>
    <el-col :span="8" v-for="(o, index) in 3" :key="o">
      <el-card :body-style="{ padding: '0px' }">
        <img :src="obj.url" class="image">
        <div class="content">

          <time class="time">{{ currentDate }}</time>

          <span>{{ obj.text}}</span>
        </div>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    name: "DataCard",
    props: {
      obj:Object
    },
    data() {
      return {
        currentDate: new Date(),
      }
    }
  }
</script>

<style scoped>


  .image {

    width: 30%;
    height: 30%;

  }
  .content{
    padding: 14px;
    display: flex;
    flex-direction: column;
  }
</style>



