<template>
  <el-container>


    <el-header>
      <div class="head_main">
        <el-tabs v-model="activeName" @tab-click="handleClick" class="tabs">
          <el-tab-pane label="第一个" name="first"/>
          <el-tab-pane label="第二个" name="second"/>
          <el-tab-pane label="第三个" name="third"/>
          <el-tab-pane label="第四个" name="fourth"/>
        </el-tabs>

        <img src="http://po8c2o94f.bkt.clouddn.com/%E5%BE%AE%E4%BF%A1%E5%9B%BE%E7%89%87_20190304163943.png"
             width="60px" height="60px" v-on:click="go2Home"/>


        <el-dropdown placement="bottom"><span class="el-dropdown-link">下拉菜单<i></i></span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>黄金糕</el-dropdown-item>
            <el-dropdown-item>狮子头</el-dropdown-item>
            <el-dropdown-item>螺蛳粉</el-dropdown-item>
            <el-dropdown-item disabled>双皮奶</el-dropdown-item>
            <el-dropdown-item divided>蚵仔煎</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>

        <el-tabs v-model="activeName" @tab-click="handleClick" class="tabs">
          <el-tab-pane label="第五个" name="five"/>

          <el-tab-pane label="第六个" name="six"/>
          <el-tab-pane label="第七个" name="seven"/>
        </el-tabs>
      </div>


    </el-header>
    <el-main>


      <keep-alive>
        <router-view></router-view>
      </keep-alive>


    </el-main>
  </el-container>

</template>


<script>

  import Tab1 from "./Tab1";
  import Tab2 from "./Tab2";
  import Tab3 from "./Tab3";


  export default {
    name: "Home",
    components: {Tab3, Tab2, Tab1},
    data() {
      return {
        activeName: 'first',
      };
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab.paneName);
        this.activeName = tab.name;
        this.$router.push(this.activeName)
      },
      go2Home() {
        console.log("----gohome")
        this.activeName = 'first';
        this.$router.push(this.activeName)
      }
    },
    comments: {
      Tab1
    },

    mounted() {
      this.$router.push(this.activeName)
    }

  }

</script>

<style scoped>

  .tabs {

    display: flex;
    width: 40%;
    padding-left: 24px;
    padding-right: 24px;
    justify-content: center;
  }

  .head_main {
    display: flex;
    justify-content: space-between;
  }


  .el-header {
    line-height: 60px;
    padding: 0;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
  }

  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;

  }

</style>



